/* Стили для хедера */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #333;
  color: #fff;
  margin-bottom: 20px; /* Добавляем отступ снизу */
}

.logo {
  font-size: 24px;
}

.logo-link {
  text-decoration: none;
  color: #fff;
  transition: color 0.3s ease;
}

.logo-link:hover {
  color: #ff4500; /* изменяем цвет при наведении */
}

/* Стили для кнопок */
.profile-button,
.logout-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #ff4500; 
  color: #fff;
  cursor: pointer;
  margin-left: 10px;
  transition: background-color 0.3s ease;
}
/* Подсветка при наведении на кнопки */
.profile-button:hover,
.logout-button:hover {
  background-color: #ff7f50; /* изменяем цвет фона при наведении */
  color: #fff; /* изменяем цвет текста при наведении */
  transition: background-color 0.3s ease; /* добавляем плавное изменение цвета */
  box-shadow: 0 0 5px rgba(255, 127, 80, 0.5); /* добавляем небольшую тень при наведении */
}




/* Стили для вкладок */
.tabs {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-bottom: 20px; /* Добавляем отступ снизу */
}

.tab {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin-right: 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.tab:hover {
  background-color: #f0f0f0; /* изменяем цвет фона при наведении */
}

.tab.active {
  background-color: #fff; /* цвет фона для активной вкладки */
  border-bottom: 2px solid #ff4500; /* добавляем подчеркивание для активной вкладки */
}

.tab-icon {
  margin-right: 5px; /* отступ между иконкой и текстом */
}

/* Стили для контента вкладок */
.tab-content {
  margin-top: 20px;
}

/* Стили для карточек опросов */
.survey-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 10px;
  padding: 20px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;

}

.survey-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.survey-card h2 {
  margin-bottom: 10px;
}

.options {
  margin-top: 20px;
}

.survey-header {
  display: flex;
  justify-content: space-between; 
  align-items: center; 
}

.toggle-button {
  background: none;
  border: none;
  cursor: pointer;
}

/* Стили для чата */
.chat {
  display: none;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.chat.active {
  display: block;
}
.carousel {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.carousel img {
  width: 80%; 
  height: auto; 
  margin: 0 auto; 
}

.slide-item {
  width: 80%; 
  margin: 0 auto;
}

.prev-button, .next-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.prev-button {
  left: 0;
}

.next-button {
  right: 0;
}

.options {
  margin-top: 20px; 
}

.vote-button {
  padding: 10px 20px;
  margin-right: 10px; 
  border: none;
  border-radius: 5px;
  background-color: #dc3545; 
  color: #fff; 
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.vote-button:hover {
  background-color: #c82333; /* Измененный красный цвет кнопки при наведении */
}

.chat-button {
  padding: 10px 20px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  background-color: #6c757d; /* Серый цвет кнопки */
  color: #fff; /* Белый цвет текста */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.chat-button:hover {
  background-color: #5a6268; /* Измененный серый цвет кнопки при наведении */
}

.expanded-content img {
  display: block; /* Устанавливаем отображение блочным элементом */
  margin: 0 auto; /* Центрируем изображение по горизонтали */
}

.create-survey-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #dc3545; /* Красный цвет кнопки */
  color: #fff; /* Белый цвет текста */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-survey-button:hover {
  background-color: #c82333; /* Измененный красный цвет кнопки при наведении */
}

/* Стили для модального окна */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный цвет фона */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Помещаем поверх всего остального */
}


.modal {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 80%;
  margin: 100vh auto; /* Отступы сверху и снизу */
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-top: 20px;
}

.modal-header h2 {
  margin: 0;
}

.close-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-size: 20px;
}

.modal-body {
  margin-bottom: 20px;
}

label {
  margin-bottom: 10px;
}

input[type="text"],
input[type="date"],
select,
textarea {
  width: calc(100% - 16px); /* Добавляем отступы по краям */
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

textarea {
  height: 100px;
}

.button-container {
  text-align: right;
}

.submit-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #a93226; /* Серо-красный цвет кнопки */
  color: #fff;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #742a21; /* Темно-красный цвет кнопки при наведении */
}
/* Личный кабинет */
.account-settings, .address-settings {
  margin-bottom: 20px;
}

.input-group {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}

.input-group label {
  width: 150px;
  margin-right: 10px;
}

.input-group input[type="text"],
.input-group input[type="email"],
.input-group input[type="password"],
.input-group input[type="date"] {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.input-group input[type="date"] {
  width: auto;
}

.add-address-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #dc3545;
  color: #fff;
  cursor: pointer;
}

.add-address-button:hover {
  background-color: #c82333;
}
